import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import React from 'react';

export class AdminView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      listTag: 1,
    };

    this.listChange1 = this.listChange1.bind(this);
    this.listChange2 = this.listChange2.bind(this);
    this.listChange3 = this.listChange3.bind(this);
    this.showList = this.showList.bind(this);
  }

  listChange1() {
    this.setState({
      listTag: 1,
    });
  }

  listChange2() {
    this.setState({
      listTag: 2,
    });
  }

  listChange3() {
    this.setState({
      listTag: 3,
    });
  }

  showList(i) {
    switch (i) {
      // 
      case 1:
        return <div></div>;
      // 
      case 2:
        return <div></div>;
      // 
      case 3:
        return <div></div>;
      default:
        break;
    }
  }

  render() {
    return (
      <div>
        {/* AppBar */}
        <Grid container direction="row" justify="center" alignItems="flex-start">
          {/*  */}
          <Grid item container direction="column" justify="flex-start" alignItems="center" style={{ backgroundColor: 'white', width: '130px', height: '200px', marginTop: '20px', marginRight: '20px' }}>
            <Grid item>
              <Button onClick={this.listChange1}>{this.state.listTag === 1 ? <span style={{ fontSize: '16px', color: 'rgb(0,132,225)' }}></span> : <span style={{ fontSize: '16px' }}></span>}</Button>
            </Grid>
            <Grid item>
              <Button onClick={this.listChange2}>{this.state.listTag === 2 ? <span style={{ fontSize: '16px', color: 'rgb(0,132,225)' }}></span> : <span style={{ fontSize: '16px' }}></span>}</Button>
            </Grid>
            <Grid item>
              <Button onClick={this.listChange3}>{this.state.listTag === 3 ? <span style={{ fontSize: '16px', color: 'rgb(0,132,225)' }}></span> : <span style={{ fontSize: '16px' }}></span>}</Button>
            </Grid>
          </Grid>

          {/*  */}
          <Grid item style={{ backgroundColor: 'white', marginTop: '20px', width: '800px' }}>
            {this.showList(this.state.listTag)}
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default AdminView;
